<template>
    <div style="text-align: -webkit-center;">
        <el-input v-model="input" placeholder="请输入文件" type="file" id="input"></el-input>
        <LineChart v-if="showing" :chartdata="chartData" :options="chartOptions"
                   style="min-width:100%; min-height: 100%"/>
        <el-card
                v-if="showReport"
                style="text-align: -webkit-center; margin: 32px; padding: 32px; width: fit-content">
            <h2>电池序号：{{battery+1}}，内阻测量结果：{{r}} 毫欧</h2>
            <span>测量日期：{{new Date(timesstamp*1000)}}</span>
            <el-divider>放电标定结果（{{tableData.length}}组）</el-divider>
            <el-table
                    :data="tableData"
                    style="width: fit-content">
                <el-table-column
                        prop="start"
                        label="起点"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="u1"
                        label="U1电压"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="end"
                        label="终点"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="u2"
                        label="U2电压"
                        width="100">
                </el-table-column>
                <el-table-column
                        prop="r"
                        label="内阻"
                        width="100">
                </el-table-column>
            </el-table>

            <el-divider>算法参数</el-divider>
            <p>AVERAGE_POINTS：{{AVERAGE_POINTS}}</p>
            <p>GODOWN_POINTS：{{GODOWN_POINTS}}</p>
            <p>GODOWN_VALUE：{{GODOWN_VALUE}}</p>
            <p>GODOWN_VALUE_PERSTEP：{{GODOWN_VALUE_PERSTEP}}</p>
            <p>OUTLIER_REMOVAL_RATIO：{{OUTLIER_REMOVAL_RATIO}}</p>
            <p>STABILITY_CHECK_POINTS：{{STABILITY_CHECK_POINTS}}</p>
            <p>STEADY_VALUE：{{STEADY_VALUE}}</p>

            <el-divider>放电参数</el-divider>
            <span>放电电阻1：{{R1}} 欧姆</span><el-divider direction="vertical"></el-divider>
            <span>放电电阻2：{{R2}} 欧姆</span><el-divider direction="vertical"></el-divider>
            <span>采样点数：{{SAMPLE_COUNT}}</span><el-divider direction="vertical"></el-divider>
            <span>采样率：{{SAMPLE_RATE}}</span><el-divider direction="vertical"></el-divider>
            <span>放电周期：{{SHOT_RATE}}</span>

            <el-divider>通讯参数</el-divider>
            <span>通讯地址：{{ADDRESS}}</span><el-divider direction="vertical"></el-divider>
            <span>波特率：{{BOUDRATE}}</span><el-divider direction="vertical"></el-divider>
            <span>停止校验：{{STOPBIT}}</span>

            <el-divider>预警参数</el-divider>
            <span>高温预警：{{HIGH_TEMP}} 摄氏度</span><el-divider direction="vertical"></el-divider>
            <span>低温预警：{{LOW_TEMP}} 摄氏度</span><el-divider direction="vertical"></el-divider>
            <span>内阻预警：{{IR_WARN}} 毫欧</span><el-divider direction="vertical"></el-divider>
            <span>超压预警：{{OVER_VOLTAGE}} V</span><el-divider direction="vertical"></el-divider>
            <span>低压预警：{{UNDER_VOLTAGE}} V</span>

        </el-card>
    </div>
</template>

<script>
    import {fromEvent} from 'file-selector';
    import LineChart from "./Chart";
    export default {
        name: "MyChart",
        components: {LineChart},
        data() {
            return {
                showReport: false,
                showing: true,
                input: '',
                chartData: {
                    labels: [],
                    datasets: [
                        {
                            label: '放电电压',
                            backgroundColor: 'rgba(32, 145, 120, 0.05)',
                            hoverBackgroundColor: 'rgba(32, 145, 120, 0.15)',
                            pointBackgroundColor: 'rgba(32, 145, 120, 1)',
                            borderColor: 'rgba(32, 145, 120, 1)',
                            borderWidth: 1,
                            pointRadius: 1,
                            data: []
                        },
                        {
                            label: '算法分析结果',
                            backgroundColor: 'rgba(180, 20, 20, 0.1)',
                            hoverBackgroundColor: 'rgba(180, 20, 20, 0.1)',
                            pointBackgroundColor: 'rgba(180, 20, 20, 1)',
                            borderColor: 'rgba(180, 20, 20, 1)',
                            showLine: false,
                            pointRadius: 5,
                            data: []
                        }
                    ]
                },
                chartOptions: {
                    responsive: true,
                    maintainAspectRatio: false
                },
                //
                timesstamp: 0,
                r: 0,
                ADDRESS: 0,
                AVERAGE_POINTS: 0,
                BOUDRATE: 0,
                GODOWN_POINTS: 0,
                GODOWN_VALUE: 0,
                GODOWN_VALUE_PERSTEP: 0,
                HIGH_TEMP: 0,
                IR_WARN: 0,
                LOW_TEMP: 0,
                OUTLIER_REMOVAL_RATIO: 0,
                OVER_VOLTAGE: 0,
                R1: 0,
                R2: 0,
                SAMPLE_COUNT: 0,
                SAMPLE_RATE: 0,
                SHOT_RATE: 0,
                STABILITY_CHECK_POINTS: 0,
                STEADY_VALUE: 0,
                STOPBIT: 0,
                UNDER_VOLTAGE: 0,
                battery: 0,
                //
                tableData: [],
            }
        },
        mounted() {
            document.getElementById('input').addEventListener('change', async evt => {
                const files = await fromEvent(evt);
                files[0].text().then(text => {
                    let rsp = JSON.parse(text.toString())
                    // 波形数据
                    this.chartData.datasets[0].data = rsp.datas;
                    // 横坐标
                    this.chartData.labels = Array.from({length: rsp.datas.length}, (v, k) => k);
                    // 放电数据
                    this.chartData.datasets[1].data = Array.from({length: rsp.datas.length});
                    // 分析结果table
                    const table = []
                    for (let i=0; i<rsp.rs.length; i++) {
                        this.chartData.datasets[1].data[rsp.starts[i]] = rsp.u1s[i]
                        this.chartData.datasets[1].data[rsp.ends[i]] = rsp.u2s[i]
                        const line = {
                            start: rsp.starts[i],
                            u1: rsp.u1s[i],
                            end: rsp.ends[i],
                            u2: rsp.u2s[i],
                            r: rsp.rs[i],
                        }
                        table.push(line)
                    }
                    this.tableData = table
                    //
                    this.timesstamp = rsp.timesstamp,
                    this.r = rsp.r
                    this.ADDRESS = rsp.parameters.ADDRESS
                    this.AVERAGE_POINTS = rsp.parameters.AVERAGE_POINTS
                    this.BOUDRATE = rsp.parameters.BOUDRATE
                    this.GODOWN_POINTS = rsp.parameters.GODOWN_POINTS
                    this.GODOWN_VALUE = rsp.parameters.GODOWN_VALUE
                    this.GODOWN_VALUE_PERSTEP = rsp.parameters.GODOWN_VALUE_PERSTEP
                    this.HIGH_TEMP = rsp.parameters.HIGH_TEMP
                    this.IR_WARN = rsp.parameters.IR_WARN
                    this.LOW_TEMP = rsp.parameters.LOW_TEMP
                    this.OUTLIER_REMOVAL_RATIO = rsp.parameters.OUTLIER_REMOVAL_RATIO
                    this.OVER_VOLTAGE = rsp.parameters.OVER_VOLTAGE
                    this.R1 = rsp.parameters.R1
                    this.R2 = rsp.parameters.R2
                    this.SAMPLE_COUNT = rsp.parameters.SAMPLE_COUNT
                    this.SAMPLE_RATE = rsp.parameters.SAMPLE_RATE
                    this.SHOT_RATE = rsp.parameters.SHOT_RATE
                    this.STABILITY_CHECK_POINTS = rsp.parameters.STABILITY_CHECK_POINTS
                    this.STEADY_VALUE = rsp.parameters.STEADY_VALUE
                    this.STOPBIT = rsp.parameters.STOPBIT
                    this.UNDER_VOLTAGE = rsp.parameters.UNDER_VOLTAGE
                    this.battery = rsp.battery

                    // 重新渲染
                    this.showReport = true
                    this.showing = false;
                    this.$nextTick(()=>{
                        this.showing = true
                    })
                    console.log(rsp);
                }).catch( err => {
                    console.log(err)
                })
            })
        }
    }
</script>

